
.search-input{
	width: 100%;
	padding: 0 .3rem;
}
.search-input input[type=text]{
	height: .3rem;
	width: 4.5rem;
	padding: .1rem .2rem;
	font-size: .24rem;
	border: none;
	outline: none;
}
.search-act,.cancle{
	float:right;
}
.search-result{
	height: calc(100vh - .8rem);
    width: 100%;
    position: absolute;
    z-index: 5;
    top: .8rem;
    background-color: rgba(0,0,0,.3);
}



